import React, { Component } from 'react';
import { fabric } from 'fabric';
import { saveAs } from 'file-saver';
import { TemplateModal } from './TemplateModal';
import { Modal, Button } from 'react-bootstrap';
import { ToolBar } from './ToolBar';
import { StickerModal } from './StickerModal';

const modals = { TEMPLATES: 'Plantillas', STICKERS: 'Stickers' };
class EditorComponent extends Component {

    constructor(props) {
        super(props);
        this.state = {
            textColor: 'black',
            showColorPicker: false,
            show: false,
            textSelected: false,
            modals: null
        };

        this._keyHandler = this._keyHandler.bind(this);
        this._showerHandler = this._showerHandler.bind(this);
        this._resizeCanvas = this._resizeCanvas.bind(this);
        this.setBackgroundImage = this.setBackgroundImage.bind(this);
        this.addAccesory = this.addAccesory.bind(this);
    }

    _resizeCanvas() {

    }
    _keyHandler(evt) {
        const c = this.canvas;
        switch (evt.key) {
            case 'Delete':
                this.deleteActiveObjects();
                break;
            default:
                break;
        }
    }

    deleteActiveObjects() {
        let c = this.canvas;
        c.getActiveObjects().map(
            activeObject => {
                return c.remove(activeObject);
            }
        )
    }

    _showerHandler(show) {
        this.setState({ showColorPicker: show })
    }

    componentDidMount() {
        const width = document.getElementById('imageContainer').clientWidth;
        var canvas = new fabric.Canvas('c');
        this.canvas = canvas;
        this.canvas.setWidth(width * .90);
        this.canvas.setHeight(width * .70);
        var image = new Image();
        image.src = require('../milos.jpg');
        image.crossOrigin = '';
        image.onload = function () {
            canvas.setBackgroundImage(new fabric.Image(image), canvas.renderAll.bind(canvas), {
                backgroundImageStretch: true,
                scaleX: canvas.width / image.width,
                scaleY: canvas.height / image.height
            });
        }

        var waterMark = new Image();
        waterMark.src = require('../will-smith.jpg');
        waterMark.onload = function () {
            var watermark = new fabric.Text("Momingo.net");
            watermark.fill = 'white';
            waterMark.name = 'waterMark';
            watermark.evented = false;
            watermark.selectable = false;
            watermark.fontSize = 20;
            watermark.top = canvas.getHeight() / 2;
            canvas.add(watermark);
        }

        document.addEventListener('keydown', this._keyHandler, false);
        window.addEventListener('resize', this._resizeCanvas, false);
    }


    componentWillUnmount() {
        document.removeEventListener('keydown', null);
    }

    addAccesory(_url) {
        var image = new Image();
        image.src = _url;
        const canvas = this.canvas;
        image.onload = function () {
            var f_img = new fabric.Image(image);
            f_img.scaleToWidth(canvas.getWidth() * .25)

            canvas.add(f_img);
        }
    }

    addText() {
        var txtCanvas = new fabric.Textbox('Escribe aqui');
        txtCanvas.setColor(this.state.textColor);
        txtCanvas.fontSize = 18;

        this.canvas.add(txtCanvas);
        this.canvas.setActiveObject(txtCanvas);
    }

    saveFile() {
        var canvas = document.getElementById('c');
        canvas.toBlob(
            function (blob) {
                saveAs(blob, 'meme.jpg');
            }
        )
    }

    reloadCanvas() {
        this.canvas.renderAndReset();
    }

    resetCanvas() {
        let c = this.canvas;
        this.canvas.getObjects().map(
            obj => {
                if (obj.type !== 'text') {
                    c.remove(obj);
                }
            }
        );
    }

    changeColor = color => {
        this.setState({ textColor: color.hex });

        this.canvas.getActiveObjects().map(
            ao => {
                switch (ao.type) {
                    case 'textbox':
                        ao.set('fill', this.state.textColor);
                        break;
                    default:
                        break;
                }

                return 0;
            }
        );
        this.reloadCanvas();
    }



    setBackgroundImage(_url) {
        var image = new Image();
        image.crossOrigin = 'Anonymous';
        image.src = _url;
        let canvas = this.canvas;
        image.onload =
            function () {
                canvas.setBackgroundImage(new fabric.Image(image), canvas.renderAll.bind(canvas), {
                    backgroundImageStretch: true,
                    scaleX: canvas.width / image.width,
                    scaleY: canvas.height / image.height
                });
            }


    }

    openCloseModal = () => {
        this.setState({ show: !this.state.show });
    }

    conditionalModal() {
        switch (this.state.modals) {
            case modals.TEMPLATES:
                return <TemplateModal onSelection={this.setBackgroundImage}>
                </TemplateModal>;
            case modals.STICKERS:
                return <StickerModal onSelection={
                    this.addAccesory
                }>
                </StickerModal>;
        }
        return null;
    }

    openStickers() {
        this.setState({ modals: modals.STICKERS });
        this.openCloseModal();
    }

    render() {
        return (
            <div>
                <nav className="navbar navbar-expand-md bg-dark navbar-dark fixed-top">
                    <a className="navbar-brand" style={{ color: "#fff" }}>Momingo.net</a>
                </nav>
                <div className="container-fluid">
                    <h3 className='text-center'>Momingo Generator</h3>
                    <br></br>
                    <div className="row mb-3">
                        <div className="col-md-3"></div>
                        <div className="col-md-6 pr-3" id="imageContainer">
                            <form className="form-inline">
                                <div>
                                    <button onClick={(evt) => {
                                        evt.preventDefault();
                                        this.setState({ modals: modals.TEMPLATES });
                                        this.openCloseModal();
                                    }} className="btn btn-success mt-2 mr-2">Buscar plantillas</button>
                                    <button onClick={(evt) => {
                                    }} className="btn btn-info mt-2">Seleccionar propia</button>
                                </div>
                            </form>


                            <ToolBar context={this} ></ToolBar>


                            <canvas id='c'></canvas>
                        </div>

                    </div>
                    <Modal size="lg" show={this.state.show} onHide={this.openCloseModal}>
                        <Modal.Header closeButton>
                            <Modal.Title>{this.state.modals}</Modal.Title>
                        </Modal.Header>
                        <Modal.Body>
                            {
                                this.conditionalModal()
                            }
                        </Modal.Body>
                        <Modal.Footer>
                            <Button variant="secondary" onClick={this.openCloseModal}>
                                Close
          </Button>
                            <Button variant="primary" onClick={() => {

                            }}>
                                Save Changes
          </Button>
                        </Modal.Footer>
                    </Modal>
                </div>
            </div>
        );
    }
}

export { EditorComponent };