import React, { Component } from "react";
import { TwitterPicker } from 'react-color';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faRedo, faStickyNote } from '@fortawesome/free-solid-svg-icons'

class ToolBar extends Component {

    constructor(props) {
        super(props);
        this.state = {
            showColorPicker: false
        };
        this.ctx = this.props.context;
    }
    showColorPicker() {
        if (this.state.showColorPicker)
            return <div className="d-flex flex-wrap">
                <TwitterPicker color={this.ctx.state.textColor ? this.ctx.state.textColor : '#fff'} triangle={"hide"} onChangeComplete={this.ctx.changeColor} colors={['#fff', '#000']} />
                <button className="btn btn-light" title="Close" onClick={() => {
                    this.setState({ showColorPicker: !this.state.showColorPicker })
                }}> <i className="fa fa-close" />
                </button>
            </div>
    }
    render() {
        return <div className="mb-3 mt-3" style={{ display: 'flex', flexDirection: 'column', alignItems: 'center', justifyContent: 'center', width: 'auto', padding: 10, border: '2px solid white', borderRadius: 10, boxShadow: '.5px .5px .5px .5px grey' }}>
            <p className="text-center"><b>Herramientas</b></p><div className="d-flex flex-wrap">
                {this.showColorPicker()}
                <input id="file-input" type="file" name="name" style={{ display: 'none' }} />
                <button className="btn btn-light" title="stickers" onClick={() => { this.ctx.openStickers(); this.ctx.addAccesory() }}><FontAwesomeIcon icon={faStickyNote} />
                </button>
                <button className="btn btn-light" title="Agrega texto" onClick={() => { this.setState({ showColorPicker: !this.state.showColorPicker }); this.ctx.addText() }}><i className="fa fa-font" />
                </button>
                <button className="btn btn-light" title="Color" onClick={() => { this.setState({ showColorPicker: !this.state.showColorPicker }); }}><i className="fa fa-tint" />
                </button>
                <button className="btn btn-light" title="SAVE" onClick={() => { this.ctx.saveFile() }}><i className="fa fa-save" /></button>
                <button className="btn btn-light" title="DELETE" onClick={() => {
                    this.ctx.deleteActiveObjects();
                }}> <i className="fa fa-trash" />
                </button>

                <button className="btn btn-light" title="Reiniciar" onClick={() => {
                    let ctx = this.ctx;
                    ctx.resetCanvas();
                }}> <FontAwesomeIcon icon={faRedo} />
                </button>
            </div>
        </div>
    }
}
export { ToolBar };