import React, { Component } from 'react';
import { templates } from '../simulator';
import { Spinner } from 'react-bootstrap';


class TemplateModal extends Component {

    constructor(props) {
        super(props);
        this.state = {
            fetching: true
        };
    }

    myfun() {
        let ctx = this;
        setTimeout(() => {
            ctx.setState({
                fetching: false
            });
        }, 3000);
    }
    componentDidMount() {
      this.myfun();
    }

    render() {
        return (
            <div className="container"> <form className="form-inline mb-3">
                <div className="form-group">
                    <label mt-2 for="search">Buscar plantilla </label>
                    <input className="form-control mt-2 ml-2 mr-1" id="search" type="text"></input>
                </div>
                <div>
                    <button className="btn btn-success mt-2 mr-2">Buscar</button>
                </div>
            </form>
                {this.state.fetching ? <div className="p-4" style={{ display: 'flex', flexDirection: 'column', alignItems: 'center', justifyContent: 'center' }}>
                    <Spinner animation="grow">
                    </Spinner>
                    <p className="text-center"><bold>Cargando...</bold></p>
                </div> : <div className="row">
                        {
                            templates.map(
                                template => {
                                    return <div className="col-md-4" onClick={
                                        () => {
                                            this.props.onSelection(template.url);
                                        }
                                    }>
                                        <p>{template.name}</p>
                                        <img alt={template.name} width="100%" src={template.url} ></img>
                                    </div>
                                }
                            )
                        }
                    </div>
                }



            </div>
        );



    }
}


export {
    TemplateModal
};