import React, { Component } from 'react';
import { Spinner } from 'react-bootstrap';
import { stickers } from '../simulator';

class StickerModal extends Component {

    constructor(props) {
        super(props);
        this.state = {
            fetching: false
        };
    }

    render() {
        return (
            <div> {this.state.fetching ? <div className="p-4" style={{ display: 'flex', flexDirection: 'column', alignItems: 'center', justifyContent: 'center' }}>
                <Spinner animation="grow">
                </Spinner>
                <p className="text-center"><bold>Cargando...</bold></p>
            </div> : <div className="row">
                    {
                        stickers.map(
                            template => {
                                return <div className="col-md-4" onClick={
                                    () => {
                                        this.props.onSelection(template.url);
                                    }
                                }>
                                    <p>{template.name}</p>
                                    <img alt={template.name} width="100%" src={template.url} ></img>
                                </div>
                            }
                        )
                    } </div>
            }
            </div>
        );
    }
}

export { StickerModal };
